import React from 'react';
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'
import './navbar.css';
export default class NavbarComponent extends React.Component{
    constructor(props) {
        super(props);
        this.closeNav.bind(this);
    }
    /**
     * function to close the navigation
     */
    closeNav() {
        document.getElementById("myNav").style.width = "0%";
        document.getElementById("myNav").style.height = "0%";
    }

    /**
     * function to open the navigation
     */
    openNav() {
        document.getElementById("myNav").style.width = "100%";
        document.getElementById("myNav").style.height = "100%";
    }
    render() {
        return (
            <div>
                <Navbar bg="dark">
                    <button onClick={this.openNav.bind(this)} className={'menuIcon'}>&#9776;</button>
                    <Navbar.Brand className={'text-light'}>
                        Gopal Oswal
                    </Navbar.Brand>
                </Navbar>
                <div id={'myNav'} className="overlay">
                    <button className={"closebtn"} onClick={this.closeNav.bind(this)}>&times;</button>
                    <div className="overlay-content">
                        <img src={require('../../assets/images/pp.jpeg')} alt="" className={'my_image'}/>
                        <h4>Gopal Oswal</h4>
                        <a onClick={this.closeNav.bind(this)} href="#home"className={'pt-4'} >Home</a>
                        <a onClick={this.closeNav.bind(this)} href="#aboutMe">About</a>
                        <a onClick={this.closeNav.bind(this)} href="#">Skills</a>
                        <a onClick={this.closeNav.bind(this)} href="#">My Work</a>
                        <a onClick={this.closeNav.bind(this)} href="#">Contact Me</a>
                    </div>
                </div>
            </div>
    )
    }
}
